.users__list{padding:1rem 2rem}

.pagination{margin:2rem;display:flex;justify-content:center;align-items:center;flex-direction:row}.pagination__page-number{display:flex;justify-content:center;align-items:center;flex-direction:row;font-weight:500;margin:0 0.3rem;border-radius:50%;width:2rem;height:2rem;color:#f8f9fb;background-color:#aa8fda;transition:all 0.4s ease-in-out}.pagination__page-number_selected{color:#97a6c3;background-color:#e4dcf3}.pagination>.btn>button:disabled{background-image:none;background-color:#c7b5e7;color:rgba(248,249,251,0.5)}

.btn{display:inline-block}.btn>button{outline:none;cursor:pointer;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:inline-block;padding:9px 15px;font-size:smaller;font-weight:700;border:none;border-radius:10px;color:transparent;color:#f8f9fb;background-image:linear-gradient(to bottom right, rgba(255,0,0,0.1), rgba(111,66,193,0.8));transition:all 0.4s ease-in-out}.btn>button:hover{color:#6f42c1}

.user{display:flex;justify-content:space-between;align-items:center;flex-direction:row;border-top:solid 1px rgba(180,180,180,0.5);padding:1.5rem 0}.user:hover .user__image{transform:scale(1.2)}.user__link{overflow:hidden;display:block;height:12vw;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:0.7rem;box-shadow:0 16px 30px -20px rgba(0,0,0,0.8),0 -16px 30px -20px rgba(0,0,0,0.8),10px 0 30px -20px rgba(0,0,0,0.8),-10px 0 30px -20px rgba(0,0,0,0.8)}.user__image{object-fit:contain;height:12vw;border-radius:0.7rem;transition:all 0.4s ease-in-out}.user__information{align-self:stretch;flex:1 1;margin:0 1rem 0 3rem;padding:0.2rem 0;border-right:solid 1px rgba(180,180,180,0.5)}.user__first-name{color:rgba(111,66,193,0.8);font-weight:700;font-size:2rem}.user__last-name{display:inline-block;color:rgba(248,249,251,0.8);font-size:2.2rem;background-color:rgba(111,66,193,0.8);padding:0.1rem 0.3rem;border-radius:5px;font-weight:500}.user__status{background-color:rgba(111,66,193,0.2);font-size:1.2rem;padding:0.1rem 0.3rem;font-weight:700;border-radius:5px;display:inline-block;margin-top:0.5rem;color:rgba(52,58,64,0.8)}.user__btn>.btn>button{width:9vw;min-width:125px}.user__btn>.btn>button:disabled{background-image:none;background-color:rgba(111,66,193,0.5);color:rgba(248,249,251,0.2)}

